interface CharacterIntro {
  name: string;
  content: string;
}

// 
type PartialDeep<T> = {
  [P in keyof T]?: T[P] extends object ? PartialDeep<T[P]> : T[P];
};

let characterIntros: Map<string, string> = new Map();
let isLoaded = false;

export async function loadCharacterIntros(): Promise<void> {
  if (isLoaded) return;
  await reloadCharacterIntros();
  isLoaded = true;
}

export async function reloadCharacterIntros(): Promise<void> {
  try {
    const worldbook = await getWorldbook('');
    console.log(':', worldbook);

    if (worldbook && worldbook.length > 0) {
      // 
      for (const entry of worldbook) {
        console.log(':', entry.name, entry.content?.substring(0, 50));
        // content 
        if (entry.name) {
          characterIntros.set(entry.name, (entry.content || '').trim());
        }
      }
      console.log('', characterIntros.size, '');
    } else {
      console.log('');
    }
  } catch (error) {
    console.log('""', error);
  }
}

export function getCharacterIntro(characterName: string): string {
  return characterIntros.get(characterName) || '';
}

export function hasCharacterIntro(characterName: string): boolean {
  return characterIntros.has(characterName) && characterIntros.get(characterName) !== '';
}

/**
 * 
 * 
 * @param characterNames 
 */
export async function syncCharacterIntrosToWorldbook(characterNames: string[]): Promise<void> {
  try {
    console.log(':', characterNames);

    let worldbook: WorldbookEntry[] = [];
    try {
      worldbook = await getWorldbook('');
    } catch (error) {
      console.log('');
    }

    // 
    const existingNames = new Set(worldbook.map(entry => entry.name));
    console.log(':', Array.from(existingNames));

    // 
    const newCharacters = characterNames.filter(name => !existingNames.has(name));
    console.log(':', newCharacters);

    if (newCharacters.length === 0) {
      console.log('');
      return;
    }

    // 
    const newEntries: PartialDeep<WorldbookEntry>[] = newCharacters.map(name => ({
      name: name,
      content: '', // 
      enabled: true,
      strategy: {
        type: 'constant' as const,
        keys: [],
        keys_secondary: { logic: 'and_any' as const, keys: [] },
        scan_depth: 'same_as_global' as const,
      },
      position: {
        type: 'before_character_definition' as const,
        role: 'system' as const,
        depth: 0,
        order: 0,
      },
      probability: 100,
      recursion: {
        prevent_incoming: false,
        prevent_outgoing: false,
        delay_until: null,
      },
      effect: {
        sticky: null,
        cooldown: null,
        delay: null,
      },
    }));

    // 
    const result = await createWorldbookEntries('', newEntries);
    console.log(` ${newCharacters.length} :`, newCharacters);
    console.log(':', result.worldbook.length);

    // 
    for (const name of newCharacters) {
      characterIntros.set(name, '');
    }
  } catch (error) {
    console.error(':', error);
  }
}

/**
 * 
 * @param characterName 
 * @param content 
 */
export async function updateCharacterIntro(characterName: string, content: string): Promise<void> {
  try {
    await updateWorldbookWith('', worldbook => {
      const entry = worldbook.find(e => e.name === characterName);
      if (entry) {
        entry.content = content;
      } else {
        // 
        worldbook.push({
          uid: Date.now(),
          name: characterName,
          content: content,
          enabled: true,
          strategy: {
            type: 'constant',
            keys: [],
            keys_secondary: { logic: 'and_any', keys: [] },
            scan_depth: 'same_as_global',
          },
          position: {
            type: 'before_character_definition',
            role: 'system',
            depth: 0,
            order: 0,
          },
          probability: 100,
          recursion: {
            prevent_incoming: false,
            prevent_outgoing: false,
            delay_until: null,
          },
          effect: {
            sticky: null,
            cooldown: null,
            delay: null,
          },
        } as WorldbookEntry);
      }
      return worldbook;
    });

    // 
    characterIntros.set(characterName, content);
    console.log(` ${characterName} `);
  } catch (error) {
    console.error(` ${characterName} :`, error);
  }
}
